.container-options-bar {
    display: flex;
    flex-direction: row;
    justify-content: center;
    position: absolute;
    top: 0px;
    left: 0px;
    background-color: white;
    border: 1px solid #c8ced8;
    z-index: 10;
}

.options-bar {
  display: flex;
}

.buttons-optionBar {
    border: none;
    animation: appear 0.5s forwards;
    background-color: transparent;
}

.image-optionBar {
  width: 23px;
  animation: appear 0.5s forwards;
}

.options {
  margin-right: 5px;
}

.height-optionsBar{
  font-family: 'Montserrat', sans-serif;
  text-align: center;
  border: 1px solid #c8ced8;
  padding-right: 5px;
  border-radius: 3px;
  padding: 5px;
  color: #001745;
}

@keyframes appear {
    0% {
      opacity: 0.1%;
      transform: scale(0.7);
    }
    100% {
      opacity: 1;
      transition: transform 0.5s;
    }
  }

.colorPicker {
  position: absolute;
  top: 35px;
  left: 150px;
}


